<template>
    <div>


        <div class="form-group row">
            <label class="col-sm-2 col-form-label">Background</label>
            <div class="col-sm-4">
                <input type="file" v-on:change="onFileChange" class="form-control-file" ref="file_input">
            </div>
            <div class="col-md-3">

                <figure class="figure">
                    <figcaption class="figure-caption">New Image</figcaption>
                    <template v-if="new_image_data">
                        <img :src="new_image_data" class="figure-img img-fluid img-thumbnail">
                    </template>
                </figure>

            </div>
            <div class="col-md-3">
                <figure class="figure">
                    <figcaption class="figure-caption">Current Image</figcaption>
                    <template v-if="image_url">
                        <img :src="image_url" class="figure-img img-fluid img-thumbnail">
                    </template>
                </figure>
            </div>
        </div>

    </div>
</template>
<script>
    import {mapTileProperties} from '../data/mappers';

    export default {
        name: 'file-upload',
        data() {
            return {
                newFileName: null,
            };
        },
        computed: {
            ...mapTileProperties({
                tile_id: 'id',
                new_image_data: 'new_image_data',
                image_url: 'front_image_url',
            }),
        },
        watch: {
            new_image_data(value) {
                if (!value) {
                    this.clearImage();
                }
            },
        },
        methods: {
            onFileChange(e) {
                let files = e.target.files || e.dataTransfer.files;
                if (!files.length)
                    return;
                this.createImage(files[0]);
            },
            createImage(file) {
                let reader    = new FileReader();
                reader.onload = (e) => {
                    this.$store.dispatch('tile/setNewImageData', e.target.result);
                };
                reader.readAsDataURL(file);
            },
            clearImage() {
                this.$refs.file_input.value = null;
            },
        },
    };
</script>



