                        <text x="144.75" y="5.5">{{item.weapon.damage | format}}</text>
                        <text x="130.75" y="5.5">{{item.weapon.aa | format}}</text>
                        <text x="116.75" y="5.5">{{item.weapon.at | format}}</text>
                        <text x="102.75" y="5.5">{{item.weapon.ap | format}}</text>
