            <g :transform="'translate(0, 196)'">

                <tile-svg-damage-track/>
            </g>
    </svg>

</template>

<script>


    import {mapAbilityGetters, mapTileGetters, mapTileProperties, mapTileWeaponGetters} from '../../data/mappers';
    import {targetingById} from '../../data/options';
    import {NONE} from '../../data/constants';
    import TileSvgDamageTrack from './tile-svg-damge-track';

    export default {
        name: 'tile-svg',
        components: {TileSvgDamageTrack},
        props: {},
        data() {
            return {
                scale: true,
            };
        },
        computed: {
            ...mapTileGetters([
                'printSubTitle',
                'evasion',
                'move',
            ]),
            ...mapAbilityGetters([
                'abilityList',
            ]),
            targeting() {
                return targetingById[this.tile_targeting_id].display_name;
            },
            assault() {
                return targetingById[this.tile_assault_id].display_name;
            },
            assaultIconCssClass() {
                return 'icon-' + targetingById[this.tile_assault_id].name;
            },
            targetingIconCssClass() {
                return 'icon-' + targetingById[this.tile_targeting_id].name;
            },
            assaultNumber() {
                return this.assault.split('D')[1];
            },
            targetingNumber() {
                return this.targeting.split('D')[1];
            },
            ...mapTileProperties({
                tile_type_id: 'tile_type_id',
                tile_class_id: 'class_id',
                tile_armor: 'armor',
                tile_tech_level_id: 'tech_level_id',
                tile_mobility_id: 'mobility_id',
                tile_targeting_id: 'targeting_id',
                tile_assault_id: 'assault_id',
                stealth: 'stealth',
                tile_ama_id: 'ama_id',
                tile_name: 'name',
                tile_front_image_url: 'front_image_url',
            }),
            ...mapTileWeaponGetters([
                'tile_weapons',
            ]),
            weaponGridY() {
                return (177.949 - this.tile_weapons.length * 9);
            },


        },
        methods: {
            weaponBlockClass(val) {
                if (val === NONE) {
                    return;
                }
                return val + '-bg';
            },
            weaponGridRowY(displayOrder) {
                return (this.weaponGridY + 9 * (displayOrder + 1));
            },
        },
        filters: {
            format(val) {
                if (val === NONE) {
                    return '-';
                }
                return val;
            },
            upper(val) {
                return val.toUpperCase();
            },
        },
    };

</script>



