<template>
    <div>

        <div class="form-group row header-row">
            <div class="col-sm-6 col-form-label">
                Info
            </div>

        </div>
        <div class="tab-content-divider"></div>
        <tile-text-input-row
            label="Name"
            v-model="tile_name"
        />
        <tile-textarea-row
            label="Flavor Text"
            v-model="flavor_text"
        />
        <file-upload/>

    </div>

</template>

<script>
    import {mapTileProperties} from '../data/mappers';

    import FileUpload from './file-upload';
    import TileTextInputRow from './tabs-info/tile-text-input-row';
    import TileTextareaRow from './tabs-info/tile-textarea-row';

    export default {
        name: 'tabs-info',
        components: {
            TileTextareaRow,
            TileTextInputRow,
            FileUpload,
        },
        computed: {
            ...mapTileProperties({
                tile_name: 'name',
                flavor_text: 'flavor_text',
            }),
        },
    };

</script>



